* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1{
  font-size: 2rem;
  font-family: cursive,fantasy;
  text-align: center;
}

h2{
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  color: white;
  padding: 20px;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 2rem 0;
}
.column{
  border-radius: 20px;
  padding: 0.5rem;
  flex-basis: var(--width);
  background-color: var(--color);
  z-index: 0;
}
.simulation-btn{
  background: linear-gradient(45deg, #f72585ff, #b5179eff, #7209b7ff, #560badff, #480ca8ff, #3a0ca3ff, #3f37c9ff, #4361eeff, #4895efff, #4cc9f0ff);
  padding: 5px;
  font-size: 1rem;
  font-weight: bold;
  background-clip: text;
  color: transparent;
  border: none;
}
.btn-container{
  --gradient-background: #f72585ff 0% 15%, var(--background) 20% 80%, #4361eeff 85% 100%;
  --background: pink;
  background: var(--background);
  width: fit-content;
  position: relative;
  margin: 10px;
}
.btn-container::after{
  content:"";
  position: absolute;
  width: calc(100% + 6px);
  height: calc(100% + 6px);
  top:-3px;
  left: -3px;
  z-index:-1;
  background: linear-gradient(45deg, var(--gradient-background));
}
.btn-container:active::after {
  background: linear-gradient(135deg, var(--gradient-background));
}
.btn-container.disabled{
  --background: grey;
}